import fs from 'file-saver'
import * as XLSX from 'xlsx'

export const xlsx = (json, fields, filename = '.xlsx') => { // xlsx
  json.forEach(item => {
    for (const i in item) {
      item[fields[i]] = item[i]

      delete item[i] // 
    }
  })
  const sheetName = filename // excel
  const wb = XLSX.utils.book_new() // SheetNamesXLSX.utils.book_new
  const ws = XLSX.utils.json_to_sheet(json, { header: Object.values(fields) }) // JS
  wb.SheetNames.push(sheetName)
  wb.Sheets[sheetName] = ws
  const defaultCellStyle = { font: { name: 'Verdana', sz: 13, color: 'FF00FF88' }, fill: { fgColor: { rgb: 'FFFFAA00' } } }// 
  const wopts = { bookType: 'xlsx', bookSST: false, type: 'binary', cellStyles: true, defaultCellStyle: defaultCellStyle, showGridLines: false } // 
  const wbout = XLSX.write(wb, wopts)
  const blob = new Blob([s2ab(wbout)], { type: 'application/octet-stream' })
  fs.saveAs(blob, filename + '.xlsx')
}

const s2ab = s => {
  let buf
  if (typeof ArrayBuffer !== 'undefined') {
    buf = new ArrayBuffer(s.length)
    const view = new Uint8Array(buf)
    for (let i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xff
    return buf
  } else {
    buf = new Array(s.length)
    for (let i = 0; i !== s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF
    return buf
  }
}
