<template>
  <el-breadcrumb :separator-icon="ArrowRight">
    <el-breadcrumb-item class="el-breadcrumb-item"></el-breadcrumb-item>
    <el-breadcrumb-item class="el-breadcrumb-item" :to="{ path: '/RequestReply' }"></el-breadcrumb-item>
    <el-breadcrumb-item class="el-breadcrumb-item"></el-breadcrumb-item>
  </el-breadcrumb>
  <el-divider/>
  <div style="display : flex;flex-direction:row">
    <div style="height:500px;width:800px">
  <el-form style="width:350px;margin-left:200px;margin-top:50px">
    <el-form-item label="">
      <el-input v-model="row[10]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[0]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[2]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[3]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[4]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[5]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[6]" disabled/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="row[7]" disabled/>
    </el-form-item>
    <el-form-item>
      <p style="margin-left:120px;font-size:30px;color:blue" v-show="pShow">{{pData}}</p>
      <el-button v-show="buttonShow" @click = "approval" size="large" style="margin-left:50px"></el-button>
      <el-button v-show="buttonShow" @click = "refuse" size="large" style="margin-left:50px"></el-button>
    </el-form-item>
  </el-form>
    </div>
  <el-table :data="vaccines" style="margin-left:100px;margin-top:50px">
    <el-table-column prop="vaccineId" label="" width="180" />
    <el-table-column prop="vaccineName" label="" width="180" />
    <el-table-column prop="vaccineNumber" label="" />
  </el-table>
  </div>
</template>

<script setup>
import { ArrowRight } from '@element-plus/icons-vue'
import { useRoute } from 'vue-router'
import { ref, reactive } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import axios from 'axios'
import Qs from 'qs'

const vaccines = reactive([])

axios.post('http://localhost:8081/graduation-project/Utils/GetVaccineNumber.php', Qs.stringify({
  institutionId: sessionStorage.getItem('currentAdminId')
})).then(function (response) {
  const count = response.data[0]
  for (let i = 1; i <= count; i++) {
    const vaccine = {
      vaccineId: response.data[i][0],
      vaccineName: response.data[i][1],
      vaccineNumber: response.data[i][2]
    }
    vaccines.push(vaccine)
  }
})

const refuse = () => {
  ElMessageBox.confirm(
    '?',
    'Warning',
    {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }
  )
    .then(() => {
      axios.post('http://localhost:8081/graduation-project/AdminMenu/RequestControl/RequestRefuse.php', Qs.stringify({
        requestId: row[10]
      })).then(function (response) {
        if (response.data === 1) {
          pShow.value = true
          buttonShow.value = false
          pData.value = ''
          ElMessage({
            type: 'success',
            message: ''
          })
        }
      })
    })
    .catch(() => {
      ElMessage({
        type: 'info',
        message: 'Delete canceled'
      })
    })
}

const approval = () => {
  ElMessageBox.confirm(
    '?',
    'Warning',
    {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }
  )
    .then(() => {
      axios.post('http://localhost:8081/graduation-project/AdminMenu/RequestControl/RequestApproval.php', Qs.stringify({
        requestId: row[10]
      })).then(function (response) {
        if (response.data === 1) {
          pShow.value = true
          buttonShow.value = false
          pData.value = ''
          ElMessage({
            type: 'success',
            message: ''
          })
        }
      })
    })
    .catch(() => {
      ElMessage({
        type: 'info',
        message: 'Delete canceled'
      })
    })
}

const route = useRoute()
const row = route.query.row
const pShow = ref(false)
const buttonShow = ref(false)
const pData = ref('')
if (row[9] === '') {
  buttonShow.value = true
} else {
  pShow.value = true
  pData.value = row[9]
}

console.log(row)
</script>

<style scoped>
.el-breadcrumb-item{
    font-size: 30px;
}
</style>
