<template>
  <div>
    <el-container class="container" >
      <el-header class="header" >
        <div class="VMSWDiv">
          <el-image :src="require('@/assets/VMSW.jpg')" :fit="cover"/>
        </div>
      </el-header>
      <el-main class='main'>
        <el-container class='loginContainer'>
          <el-header height="30px" class="loginHeader"></el-header>
          <el-divider class="divider" />
          <el-main>
            <el-form :model="form" label-width="60px">
              <el-form-item label="" style="margin-top:30px">
                <el-cascader v-model="form.area" :options="options" placeholder=""/>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.name" placeholder="" :prefix-icon="Avatar" clearable/>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.password" placeholder="" :prefix-icon="Lock" show-password clearable/>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" style="width:80%" @click="login"></el-button>
              </el-form-item>
            </el-form>
          </el-main>
        </el-container>
      </el-main>
      <el-footer class='footer'>
        <p></p>
      </el-footer>
    </el-container>
  </div>
</template>
<script setup>
import { Avatar, Lock } from '@element-plus/icons-vue'
import axios from 'axios'
import { reactive, ref } from 'vue'
import Qs from 'qs'
import { ElMessage } from 'element-plus'
import { useRouter } from 'vue-router'

const router = useRouter()

const form = reactive({
  area: [],
  name: '',
  password: ''
})
const areaTool = ref('')

const login = () => {
  if (form.area.length !== 0) {
    areaTool.value = form.area[0] + '_' + form.area[1] + '_' + form.area[2]
  } else {
    areaTool.value = ''
  }
  axios.post('http://localhost:8081/graduation-project/AdminLogin/AdminLoginResult.php', Qs.stringify({
    area: areaTool.value,
    name: form.name,
    password: form.password
  })).then(function (response) {
    console.log(response.data)
    switch (response.data) {
      case 'A': {
        ElMessage.error('.')
        break
      }
      case 'B': {
        ElMessage.error('.')
        break
      }
      case 'C': {
        ElMessage.error('.')
        break
      }
      case 'D': {
        ElMessage.error('.')
        break
      }
      case 'E': {
        ElMessage.error('.')
        break
      }
      default: {
        sessionStorage.setItem('currentAdminId', response.data)
        router.push({
          path: '/SystemIntroduce'
        })
      }
    }
  })
}

const options = [
  {
    value: 0,
    label: '',
    children: [
      {
        value: 0,
        label: '',
        children: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          }
        ]
      }
    ]
  }
]
</script>

<style scoped>
.VMSWDiv{
  height: 60px;
  width: 120px;
  position: relative;
  top:12px;
  left: 80px;
}
.container{
  height: 730px;
}
.header{
  background-color: black;
}
.main{
  background-color: white;
  background-image: url(@/assets/loginBody.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center, 0;
}
.footer{
  background-color: black;
}
.loginContainer{
  position: relative;
  margin-left: 40%;
  border-radius: 20px;
  border: 2px solid black;
  background-color: white;
  opacity: 0.8;
  height: 450px;
  width: 450px;
  top: 20px;
}
.loginHeader{
  font-size: 30px;
}
</style>
