<template>
  <div>
      <el-container class="container">
        <el-header height="70px" class="header">
          <p class="pPosition">| </p>
        </el-header>
        <el-divider></el-divider>
        <el-main class="main">
          <el-steps :active="active" finish-status="success" style="margin-top:30px" align-center>
            <el-step title="" />
            <el-step title="" />
            <el-step title="" />
            <el-step title="" />
          </el-steps>
          <el-form :model="form" >
            <el-form-item v-show="show.name">
              <el-input v-model="form.name" placeholder="" clearable/>
            </el-form-item>
            <el-form-item v-show="show.sex">
              <el-radio v-model="form.sex" label="1"></el-radio>
              <el-radio v-model="form.sex" label="0"></el-radio>
            </el-form-item>
            <el-form-item v-show="show.IDSelect">
              <el-select v-model="form.IDSelect" placeholder="">
                <el-option v-for="item in indentify"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"/>
              </el-select>
            </el-form-item>
            <el-form-item v-show="show.IDNumber">
              <el-input v-model="form.IDNumber" placeholder="" clearable/>
            </el-form-item>
            <el-form-item v-show="show.area">
              <el-cascader v-model="form.area" :options="options" placeholder=""/>
            </el-form-item>
            <el-form-item v-show="show.phoneNumber">
              <el-input v-model="form.phoneNumber"  placeholder="" clearable/>
            </el-form-item>
            <el-form-item v-show="show.email">
              <el-input v-model="form.email" placeholder="" clearable/>
            </el-form-item>
            <el-form-item v-show="show.password">
              <el-input v-model="form.password"  placeholder="" clearable/>
            </el-form-item>
            <el-form-item v-show="show.againPassword">
              <el-input v-model="form.againPassword" placeholder="" clearable/>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" style="margin-top: 12px" @click="firstNext" v-show="show.firstNextButton">Next step</el-button>
              <el-button type="primary" style="margin-top: 12px" @click="secondLast" v-show="show.secondLastButton">Last step</el-button>
              <el-button type="primary" style="margin-top: 12px" @click="secondNext" v-show="show.secondNextButton">Next step</el-button>
              <el-button type="primary" style="margin-top: 12px" @click="thirdLast" v-show="show.thirdLastButton">Last step</el-button>
              <el-button type="primary" style="margin-top: 12px" @click="submit" v-show="show.submitButton"></el-button>
            </el-form-item>
          </el-form>
          <el-result icon="success" title="" sub-title="" v-show="show.registerSuccess">
            <template #extra>
              <el-link type="success" href="./#/UserLogin"></el-link>
            </template>
          </el-result>
        </el-main>
      </el-container>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import axios from 'axios'
import Qs from 'qs'
import { ElMessage } from 'element-plus'

const show = reactive({
  name: true,
  sex: true,
  IDSelect: true,
  IDNumber: true,
  area: true,
  phoneNumber: false,
  email: false,
  password: false,
  againPassword: false,
  firstNextButton: true,
  secondLastButton: false,
  secondNextButton: false,
  thirdLastButton: false,
  submitButton: false,
  registerSuccess: false
})

const form = reactive({
  name: '',
  sex: '',
  IDSelect: '',
  IDNumber: '',
  area: [],
  phoneNumber: '',
  email: '',
  password: '',
  againPassword: ''
})

const indentify = [
  {
    value: 0,
    label: ''
  },
  {
    value: 1,
    label: ''
  },
  {
    value: 2,
    label: ''
  },
  {
    value: 3,
    label: ''
  },
  {
    value: 4,
    label: ''
  }
]

const options = [
  {
    value: 0,
    label: '',
    children: [
      {
        value: 0,
        label: '',
        children: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          }
        ]
      }
    ]
  }
]

const active = ref(0)

const firstNext = () => {
  axios.post('http://localhost:8081/graduation-project/UserRegister/FirstStep.php', Qs.stringify({
    name: form.name,
    sex: form.sex,
    IDType: form.IDSelect,
    IDNumber: form.IDNumber,
    area: form.area
  })).then(function (response) {
    switch (response.data) {
      case 0: {
        show.name = false
        show.sex = false
        show.IDSelect = false
        show.IDNumber = false
        show.area = false
        show.phoneNumber = true
        show.email = true
        show.firstNextButton = false
        show.secondLastButton = true
        show.secondNextButton = true
        active.value++
        break
      }
      case 1: {
        ElMessage.error('.')
        break
      }
      case 2: {
        ElMessage.error('.')
        break
      }
      case 3: {
        ElMessage.error('.')
        break
      }
      case 4: {
        ElMessage.error('.')
        break
      }
      case 5: {
        ElMessage.error('.')
        break
      }
      case 6: {
        ElMessage.error('.')
        break
      }
    }
  })
}

const secondNext = () => {
  axios.post('http://localhost:8081/graduation-project/UserRegister/SecondStep.php', Qs.stringify({
    phoneNumber: form.phoneNumber,
    email: form.email
  })).then(function (response) {
    switch (response.data) {
      case 0: {
        show.phoneNumber = false
        show.email = false
        show.password = true
        show.againPassword = true
        show.nextButton = false
        show.submitButton = true
        show.secondLastButton = false
        show.secondNextButton = false
        show.thirdLastButton = true
        show.submitButton = true
        active.value++
        break
      }
      case 1: {
        ElMessage.error('.')
        break
      }
      case 2: {
        ElMessage.error('.')
        break
      }
      case 3: {
        ElMessage.error('.')
        break
      }
      case 4: {
        ElMessage.error('.')
        break
      }
    }
  })
}

const secondLast = () => {
  show.name = true
  show.sex = true
  show.IDSelect = true
  show.IDNumber = true
  show.area = true
  show.phoneNumber = false
  show.email = false
  show.firstNextButton = true
  show.secondLastButton = false
  show.secondNextButton = false
  active.value--
}

const thirdLast = () => {
  show.phoneNumber = true
  show.email = true
  show.password = false
  show.againPassword = false
  show.secondLastButton = true
  show.secondNextButton = true
  show.thirdLastButton = false
  show.submitButton = false
  active.value--
}

const submit = () => {
  axios.post('http://localhost:8081/graduation-project/UserRegister/RegisterResult.php', Qs.stringify({
    name: form.name,
    sex: form.sex,
    IDType: form.IDSelect,
    IDNumber: form.IDNumber,
    area: form.area,
    phoneNumber: form.phoneNumber,
    email: form.email,
    password: form.password,
    againPassword: form.againPassword
  })).then(function (response) {
    switch (response.data) {
      case 0: {
        show.password = false
        show.againPassword = false
        show.thirdLastButton = false
        show.submitButton = false
        show.registerSuccess = true
        break
      }
      case 1: {
        ElMessage.error('.')
        break
      }
      case 2: {
        ElMessage.error('.')
        break
      }
      case 3: {
        ElMessage.error('.')
        break
      }
      case 4: {
        ElMessage.error('.')
        break
      }
    }
  })
}
</script>

<style scoped>
.container{
  position: fixed;
  top:0;
  height: 750px;
  width: 100%;
}
.header{
  font-size: 20px;
  text-align: left;
}
.pPosition{
  margin-left: 250px;
}
.main{
  width: 100%;
  height: 100%;
}
.el-divider{
  margin: 0px 0;
  background: 0 0;
  border-top: 1px solid #E6EBF5;
}
.el-form-item{
  position: relative;
  top :100px
}
.el-form{
  width: 360px;
  position: relative;
  margin: 0 auto;
}
.example-block {
  margin: 1rem;
}
.example-demonstration {
  margin: 1rem;
}
</style>
